/*- Typograpy and resets -*/
  body {
    font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 100%;
    background-color: #fff;
  }
/*- Card container -*/
  .card-container {
    position: relative;
    z-index: 1;
    margin: 32px auto;
    max-width: 200px;
    height: auto;
    -webkit-perspective: 1000px;
            perspective: 1000px;
  }
  
  img {
    max-width: 100%;
    max-height: 100%;
  }
  
  /*- Card body -*/
  .card-body {
    width: 100%;
    height: 100%;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: all .7s linear;
    transition: all .7s linear;
  }
  
  /*- Flipping the card -*/
  .card-container:hover .card-body {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }
  
  .card-container:hover > .card-body > .side-front {
    opacity: 0;
    visibility: hidden;
    -webkit-transition: opacity 1s ease-in, visibility .75s linear;
    transition: opacity 1s ease-in, visibility .75s linear;
  }
  
  /*- Card sides -*/
  .card-side {
    position: absolute;
    top: 0;
    overflow: hidden;
    width: auto;
    color: #212121;
    background-color: #fff;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
  }
  
  /*- Front side -*/
  .side-front [class^=col-xs]:first-of-type {
    padding-left: 0;
  }
  
  /*- Back side -*/
  .side-back {
    z-index: 2;
    padding-top: 52px;
    padding-right: 2px;
    padding-left: 20px;
    text-align: center;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }
  
.root {
    max-width: 200px;
}
.media {
    height: 50px;
}
  
  
